// aws lambda 호출하여 계산 결과 가져오기
function call(data, onLoad, onError = (data) => alert("오류 발생!", data)) {
  fetch(
    "https://li6ctgphqglz6ol7cuo5qik3dm0mphxc.lambda-url.us-east-1.on.aws/",
    {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: window.btoa(window.encodeURI(window.JSON.stringify(data))),
    }
  )
    .then((response) => {
      return response.json();
    })
    .then(onLoad)
    .catch((err) => {
      console.log("오류발생", err);
      onError(err);
    });
}

export default call;
